import _ from 'lodash';
import * as PIXI from 'pixi.js';
import WebFont from 'webfontloader';
import {Slide} from './slide';
import Spinner from "./spinner";
import {once} from "../lib/utils";
import EventEmitter from "events";

const SLIDE_TIME = 5000;

export default class Slider extends EventEmitter {

    constructor(options={}) {

        super();

        this._slides = [];

        this._options = options;

        this._pixi = new PIXI.Application({
            forceCanvas: true,
            width: options.width,
            height: options.height,
            backgroundColor: this.getCustomization('backgroundColor')
        });

        this._spinner = new Spinner();
        this._spinner.visible = false;
        this._spinner.x = options.width / 2;
        this._spinner.y = options.height / 2;

        this._slidesContainer = new PIXI.Container();

        this._pixi.stage.addChild(this._slidesContainer);
        this._pixi.stage.addChild(this._spinner);

        this._pixi.ticker.add(() => {
            if (this._currentSlide) {
                this._currentSlide.animate();
            }
        });

        this._index = -1;
        this._totalSlides = 0;
        this._totalInvalidSlides = 0;

        this._timer = new RecurringTimer(() => {
            if (this._index === this._slides.length - 1) {
                this._slidesContainer.removeChildren();
            }
            this.next();
        }, SLIDE_TIME);

        this._ready = this._preload();

        this.add({
            ...this._options.intro
        },{
            type: 'intro'
        });

        this.next();

    }

    ready() {
        return this._ready;
    }

    pause() {

        if (this._paused) {
            return;
        }

        this._paused = true;
        this._playing = false;
        this._timer.pause();
        this._currentSlide.pause();
    }

    async play(time) {

        if (this._playing) {
            return;
        }

        if (!this._currentSlide) {
            await this.next();
        }

        if (this._currentSlide) {
            this._currentSlide.play();
            if (time) {
                this._currentSlide.currentTime = time;
            }
            this._timer.start(time);

            this._playing = true;
            this._paused = false;
        }
    }

    stop() {

        this._timer.stop();
        this._currentSlide && this._currentSlide.stop();
        this._playing = false;
        this._paused = false;
        this._index = -1;
        this._currentSlide = null;
        this._slidesContainer.removeChildren();
    }

    get view() {
        return this._pixi.view;
    }

    get duration() {
        return Math.max(0, (this._slides.length - this._totalInvalidSlides) * SLIDE_TIME / 1000);
    }

    get currentTime() {
        let index = this._index - this._totalInvalidSlides;
        return Math.max(0, (index * SLIDE_TIME + (this._currentSlide ? this._currentSlide.currentTime : 0)) / 1000);
    }

    set currentTime(value) {

        let index = value / (SLIDE_TIME / 1000);

        let time = (1-(Math.ceil(index) - index)) * SLIDE_TIME;

        index = Math.floor(index);

        let wasPlaying = this._playing;

        this.stop();

        this._index = index - 1;

        if (wasPlaying) {
            this.play(time);
        }


    }

    async next() {

        if (!this._slides.length) {
            this._spinner.show();
            await once(this, 'slide');
            this._spinner.hide();
        }

        this._index = (this._index+1) % this._slides.length;

        let nextSlide = this._slides[this._index];

        this._currentSlide = nextSlide;

        if (!nextSlide) {
            throw new Error("no slides to show!");
        }

        if (nextSlide.invalid) {
            if (nextSlide.isIntroSlide() && !this._totalSlides) {
                await once(this, 'slide');
            }
            return this.next();
        }

        if (!nextSlide.ready) {
            this._spinner.show();
            await nextSlide.isReady();
            this._spinner.hide();
        }

        if (this._slidesContainer.children.length > 2) {
            this._slidesContainer.removeChildAt(0);
        }

        if (this._playing) {
            nextSlide.restart();
        }

        this._slidesContainer.addChild(nextSlide.container);

        return nextSlide;

    }

    async add(slides, options) {

        if (!_.isArray(slides)) slides = [slides];

        let results = [];

        for(let raw of slides) {

            let slide = this._createSlide(raw, options);

            if (slide.invalid) {
                this._totalInvalidSlides++;
            }

            slide.on("click", this.onSlideClick.bind(this));

            this._slides.push(slide);

            if (!slide.isIntroSlide()) {
                this._totalSlides++;
            }

            results.push(slide);

            this.emit('slide', slide);
        }

        return results[0];
    }

    async getSlides(n) {

        while(this._totalSlides < n) {
            await once(this, 'slide')
        }

        return this._slides.slice(1, n+1);
    }

    getCustomization(name) {
        if (this._options.customize) {
            return this._options.customize[name];
        }
    }

    _createSlide(raw, options) {
        return new Slide(this, raw, {
            ...options,
            width: this._options.width,
            height: this._options.height,
            theme: this._options.theme,
            backgroundColor: this._options.backgroundColor,
            duration: SLIDE_TIME
        });
    }

    onSlideClick(slide) {
        window.location.href = slide.link;
    }

    _preload() {

        return Promise.all([
            loadFonts()
        ]);

        function loadFonts() {
            return new Promise((resolve) => {
                WebFont.load({
                    google: {
                        families: ['Roboto']
                    },
                    active: resolve
                });
            })
        }
    }
}

class RecurringTimer {

    constructor(callback, delay) {
        this._callback = callback;
        this._delay = delay;

        this.stop();
    }

    pause() {
        if (this._start) {
            window.clearTimeout(this._timerId);
            this._remaining -= new Date() - this._start;
        }
    }

    start(forward) {
        this._start = new Date();
        window.clearTimeout(this._timerId);
        if (forward) {
            this._remaining -= forward;
        }
        this._timerId = window.setTimeout(() => {
            this._remaining = this._delay;
            this.start();
            this._callback();
        }, this._remaining);
    }

    stop() {
        this._remaining = this._delay;
        window.clearTimeout(this._timerId);
    }

    get currentTime() {
        return new Date() - this._start;
    }
}