

export default class ContentAdapterBase {

    constructor() {
        this._handlers = {};
    }

    on(event, cb) {
        if (!this._handlers[event]) {
            this._handlers[event] = [];
        }
        this._handlers[event].push(cb);
    }

    emit(event, ...args) {
        if (this._handlers[event]) {
            for(let handler of this._handlers[event]) {
                handler.apply(this, args);
            }
        }
    }

    static _throw(message) {
        throw new Error(`ContentAdapter: ${message}`);
    }
}