import * as PIXI from "pixi.js";
import Loader from './loader';
import TimelineLite from "gsap/TimelineLite";
import TweenLite from "gsap/TweenLite";

const TRANSITIONS = ['polkadots','windowSlice'];

export class Cover extends PIXI.Sprite {

    constructor(options) {

        super();

        this._step = 1;

        this._options = _.defaults(options, {
            size: 'cover',
            transition: TRANSITIONS[0]
        });

        TRANSITIONS.push(TRANSITIONS.shift());

        this._canvas = document.createElement('canvas');
        this._canvas.width = options.width;
        this._canvas.height = options.height;

        this._ctx = this._canvas.getContext('2d');

        this.width = options.width;
        this.height = options.height;

    }

    set(options) {
        Object.assign(this._options, options);
    }

    set image(image) {
        this._image = image;
        if (!this.width) {
            this.width = image.width;
        }
        if (!this.height) {
            this.height = image.height;
        }
        this._draw();
    }

    get image() {
        return this._image;
    }

    set transitionStep(step) {
        this._step = step;
        this._draw();
    }

    get trasitionStep() {
        return this._step;
    }

    loadImage(url) {
        Loader.loadImage(url).then((image) => {
            this.image = image;
        });
    }

    get size() {
        return this._options.size;
    }
    set size(value) {
        this._options.size = value;
    }

    _draw() {

        if (!this._image) {
            return;
        }

        let image = this._image;
        let width = this.width;
        let height = this.height;

        this._ctx.save();

        this._canvas.width = width;
        this._canvas.height = height;

        if (this._step < 1) {
            let imageData = this._ctx.createImageData(width, height);

            let d = imageData.data;
            for (let i=0; i<=width*height*4; i+=4) {
                d[i] = 255;
                d[i+1] = 0;
                d[i+2] = 0;
                d[i+3] = this._trans({x:Math.floor(i/4)%width, y:Math.floor(i/4/width)%height}, this._step, this._options.transition) ? 255 : 0;
            }

            this._ctx.putImageData(imageData, 0,0);

            this._ctx.globalCompositeOperation="source-in";
        }

        switch(this._options.size) {
            case 'cover':
                drawImageProp(this._ctx, image);
                break;
            case 'contain':
                drawImageScaled(this._ctx, image);
                break;
            default:
                this._ctx.drawImage(
                    image,
                    width/2 - image.width/2,
                    height/2 - image.height/2,
                    image.width,
                    image.height);
                break;
        }

        this._ctx.restore();

        this.texture = new PIXI.Texture.fromCanvas(this._canvas);
    }

    _trans(uv, progress, type) {
        switch(type) {
            case 'polkadots' :
                let dots = 20;
                let r = this.width/dots;
                let h = Math.floor(uv.x/r)*r + r/2;
                let k = Math.floor(uv.y/r)*r + r/2;
                let d = distance(uv, {x:0,y:0});
                let md = Math.sqrt(Math.pow(this.width,2) + Math.pow(this.height,2));
                let rm = r / Math.sqrt(2);
                return distance({x:h,y:k}, uv) < rm * Math.pow(progress,3) / d * md;
            case 'windowSlice' :
                let slices = 15;
                let slice_w = this.width/slices;
                let slice_num = Math.floor(uv.x/slice_w);
                let slice_end_x = (slice_num + 1) * slice_w;
                let speed = 1.5;
                let slice_progress_start = Math.sqrt((slice_num + 1)/slices/speed,2);
                const slice_progress_duration = 0.1;
                if (slice_progress_start + slice_progress_duration < progress) return true;
                if (slice_progress_start < progress) {
                    let progress_from_slice_start = progress - slice_progress_start;
                    let distance_from_slice_end = slice_end_x - uv.x;
                    if (distance_from_slice_end < progress_from_slice_start * slice_w / slice_progress_duration) return true;
                }
                return false;
            case 'bowtie':
                break;

        }

    }

}

function drawImageScaled(ctx, img) {
    var canvas = ctx.canvas ;
    var hRatio = canvas.width  / img.width    ;
    var vRatio =  canvas.height / img.height  ;
    var ratio  = Math.min ( hRatio, vRatio );
    var centerShift_x = ( canvas.width - img.width*ratio ) / 2;
    var centerShift_y = ( canvas.height - img.height*ratio ) / 2;
    ctx.clearRect(0,0,canvas.width, canvas.height);
    ctx.drawImage(img, 0,0, img.width, img.height,
        centerShift_x,centerShift_y,img.width*ratio, img.height*ratio);
}

function drawImageProp(ctx, img, x, y, w, h, offsetX, offsetY) {

    if (arguments.length === 2) {
        x = y = 0;
        w = ctx.canvas.width;
        h = ctx.canvas.height;
    }

    // default offset is center
    offsetX = typeof offsetX === "number" ? offsetX : 0.5;
    offsetY = typeof offsetY === "number" ? offsetY : 0.5;

    // keep bounds [0.0, 1.0]
    if (offsetX < 0) offsetX = 0;
    if (offsetY < 0) offsetY = 0;
    if (offsetX > 1) offsetX = 1;
    if (offsetY > 1) offsetY = 1;

    var iw = img.width,
        ih = img.height,
        r = Math.min(w / iw, h / ih),
        nw = iw * r,   // new prop. width
        nh = ih * r,   // new prop. height
        cx, cy, cw, ch, ar = 1;

    // decide which gap to fill
    if (nw < w) ar = w / nw;
    if (Math.abs(ar - 1) < 1e-14 && nh < h) ar = h / nh;  // updated
    nw *= ar;
    nh *= ar;

    // calc source rectangle
    cw = iw / (nw / w);
    ch = ih / (nh / h);

    cx = (iw - cw) * offsetX;
    cy = (ih - ch) * offsetY;

    // make sure source rectangle is valid
    if (cx < 0) cx = 0;
    if (cy < 0) cy = 0;
    if (cw > iw) cw = iw;
    if (ch > ih) ch = ih;

    // fill image in dest. rectangle
    ctx.drawImage(img, cx, cy, cw, ch,  x, y, w, h);
}

function distance(v1, v2) {
    let x = v1.x - v2.x;
    let y = v1.y - v2.y;
    return Math.sqrt(x*x + y*y);
}