import * as PIXI from "pixi.js";
import _ from "lodash";
import EventEmitter from "events";
import SlideTheme from "./slide-theme";

export class Slide extends EventEmitter {

    constructor(slider, data, opt={}) {

        super();

        this._slider = slider;

        this._container = new PIXI.Container();

        this._options = _.defaults(opt, {
            theme: 'default',
            backgroundColor: 0x00
        });

        this._container.interactive = true;
        this._container.click = (event) => {
            if (data.link) {
                this.emit("click", data);
            }
            event.stopPropagation();
        };

        this._data = data;

        this._theme = SlideTheme.create(this, this._options.theme);

        if (!this.invalid) {

            this._pReady = this._theme.isReady();

            Promise.resolve(this._pReady).then(() => {
                this._ready = true;
            });
        }

        this._timer = new Timer();

    }

    get duration() {
        return this._options.duration;
    }

    get invalid() {
        return this._theme.invalid;
    }

    get slider() {
        return this._slider;
    }

    get container() {
        return this._container;
    }

    get width() {
        return this._options.width;
    }

    get height() {
        return this._options.height;
    }

    get coverSize() {
        return this._options.coverSize;
    }

    get backgroundColor() {
        return this._options.backgroundColor;
    }

    get cover() {
        return this._data.cover;
    }

    get data() {
        return this._data;
    }

    get currentTime() {
        return this._timer.currentTime;
    }
    set currentTime(value) {
        this._timer.currentTime = value;
        this._theme.progress(value/this.duration);
    }

    isIntroSlide() {
        return this._options.type === 'intro';
    }

    get ready() {
        return this._ready;
    }

    isReady() {
        return this._pReady;
    }

    pause() {
        this._timer.pause();
        this._theme.pause();
    }

    play() {
        this._timer.start();
        this._startTime = new Date();
        this._theme.play();
    }

    stop() {
        this._timer.stop();
        this._theme.stop();
    }

    restart() {
        this._timer.restart();
        this._theme.restart();
    }

    get animateTransition() {
        return typeof this._options.animateTransition === 'undefined' || this._options.animateTransition;
    }

    get animateCover() {
        return typeof this._options.animateCover === 'undefined' || this._options.animateCover;
    }

    animate() {
        this._theme.animate();
    }
}

class Timer {

    constructor() {
        this.stop();
    }

    start() {
        this._startTime = new Date();
    }

    pause() {
        this._time += this._startTime ? new Date() - this._startTime : 0;
        this._startTime = null;
    }

    stop() {
        this.pause();
        this._time = 0;
    }

    restart() {
        this.stop();
        this.start();
    }

    get currentTime() {
        return this._time + (this._startTime ? (new Date() - this._startTime) : 0);
    }
    set currentTime(value) {
        this._startTime = new Date();
        this._time = value;
    }
}