import * as PIXI from 'pixi.js';
import TweenLite from "gsap/TweenLite";
import TimelineLite from "gsap/TimelineLite";

export default class Spinner extends PIXI.Container {

    constructor() {
        super();

        this.diameter = 30;
        this.padding = 10;

        let outer = this.diameter + this.padding;

        let bg = new PIXI.Graphics();
        bg.beginFill(0x00, 0.5);
        bg.drawRect(-outer/2, -outer/2, outer, outer);

        let sprite = this._createSprite();
        sprite.anchor.set(0.5);
        sprite.rotation = 0.5;

        this.addChild(bg);
        this.addChild(sprite);

        this._animation = new TimelineLite({
            onComplete:function() {
                this.restart();
            }
        });

        this._animation.add(TweenLite.to(sprite, 0.5, {rotation: Math.PI*2, ease: 'linear'}));

    }

    hide() {
        this._animation.pause();
        this.visible = false;
    }

    show() {
        this._animation.play();
        this.visible = true;
    }

    _createSprite() {

        let canvas = document.createElement('canvas');
        let ctx = canvas.getContext("2d");

        let opacity, d = canvas.width = canvas.height = this.diameter;

        ctx.translate(d/2, d/2);
        ctx.rotate(Math.PI * 360/360);
        ctx.lineWidth = Math.ceil(d / 50);
        ctx.lineCap = 'square';

        for(var i=0; i<=360; i++) {
            ctx.save();

            ctx.rotate((Math.PI * i/180));
            //ctx.translate(-ctx.lineWidth/2, ctx.lineWidth/2);

            ctx.beginPath();
            ctx.moveTo(0, 0);
            opacity = (360 - (i * 0.95)) / 360;
            ctx.strokeStyle = 'rgba(255,255,255,' + opacity.toFixed(2) +')';
            ctx.lineTo(0, d + 30);
            ctx.stroke();
            ctx.closePath();

            ctx.restore();
        }

        ctx.globalCompositeOperation ='source-out';
        ctx.beginPath();
        ctx.arc(0, 0, d/2, 2 * Math.PI, false);
        ctx.fillStyle = 'white';
        ctx.fill();

        ctx.globalCompositeOperation ='destination-out';
        ctx.beginPath();
        ctx.arc(0, 0, (d/2) * .9, 2 * Math.PI, false);
        ctx.fill();

        let texture = new PIXI.Texture.fromCanvas(canvas);

        return new PIXI.Sprite(texture);
    }


}