import Audio from "./audio";
import Slider from "./slider";
import {DynamicContentAdapter} from "./adapter";
import _ from "lodash";

export default class ContentGenerator {

    constructor(opts={}) {

        let options = this._options = _.defaults(opts, {
            delay: 0,
            volume: 1,
            muted: false
        });

        this._audio = new Audio(options.audio);

        this._slider = new Slider({
            ...options
        });

        this._domElement = this._createDomElement();

        this._domElement.appendChild(this._slider.view);



        this._ready = Promise.all([
            this._audio.ready(),
            this._slider.ready()
        ]);

        this._ready.then(() => {
            if (this.autoplay) {
                this.play({autoplay:true});
            }
        });

        this._init();
    }

    async _init() {

        await this._ready;

        let options = this._options;

        if (options.content) {

            if (_.isArray(options.content)) {

                this._dispatch('loadedmetadata');

                this._slider.add(options.content);
            }

            else {

                let dynamicContent = new DynamicContentAdapter(options.content);

                dynamicContent.on('content', (content) => {
                    this._slider.add(content);
                });

                setTimeout(() => {
                    dynamicContent.refresh();
                }, options.delay);
            }
        }
    }

    getDomElement() {
        return this._domElement;
    }

    get autoplay() {
        return this._options.autoplay;
    }
    set autoplay(value) {
        this._options.autoplay = value;
    }

    pause() {
        this._slider.pause();
        this._audio.pause();
    }

    play({autoplay}={}) {
        this._ready.then(() => {
            this._slider.play();
            this._audio.play(autoplay);
        });
    }

    stop() {
        this._slider.stop();
        this._audio.stop();
    }

    get muted() {
        return this._options.muted;
    }
    set muted(muted) {
        this._options.muted = muted;
        if (muted) {
            this._audio.volume = 0;
        }
        else {
            this._audio.volume = this.volume;
        }
    }

    get volume() {
        return this._options.volume;
    }
    set volume(vol) {
        this._options.volume = vol;
        if (!this.muted) {
            this._audio.volume = vol;
        }
    }

    get currentTime() {
        return this._slider.currentTime;
    }
    set currentTime(value) {
        this._slider.currentTime = value;
    }

    get duration() {
        return this._slider.duration;
    }

    _createDomElement() {

        let elm = document.createElement("div");

        Object.defineProperties(elm, {
            load: {
                value: () => {

                }
            },
            play: {
                value: () => {
                    this.play();
                }
            },
            pause: {
                value: () => {
                    this.pause();
                }
            },
            stop: {
                value: () => {
                    this.stop();
                }
            },
            autoplay: {
                set: (value) => {
                    this.autoplay = value;
                },
                get: () => {
                    return this.autoplay;
                }
            },
            muted: {
                set: (value) => {
                    this.muted = value;
                },
                get: () => {
                    return this.muted;
                }
            },
            volume: {
                set: (value) => {
                    this.volume = value;
                },
                get: () => {
                    return this.volume;
                }
            },
            duration: {
                get: () => {
                    return this.duration;
                }
            },
            currentTime: {
                get: () => {
                    return this.currentTime;
                },
                set: (value) => {
                    this.currentTime = value;
                }
            }
        });

        return elm;
    }

    _dispatch(type) {
        this._domElement.dispatchEvent(new Event(type))
    }

}