import _ from 'lodash';
import {Container,Graphics,TextMetrics} from "pixi.js";
import TimelineLite from "gsap/TimelineLite";
import TweenLite from "gsap/TweenLite";
import {Cover} from "./cover";

export default class SlideTheme {

    static create(slide, themeName) {
        let {'default':theme} = require(`./themes/${themeName}-theme`);
        return new theme(slide);
    }

    constructor(slide) {

        this._slide = slide;

        const animations = new TimelineLite({ paused: true });

        this._animations = animations;

        let cover = new Cover({
            size: slide.coverSize,
            width: slide.width,
            height: slide.height,
        });

        this._coverContainer = new PIXI.Container();
        this._coverContainer.addChild(cover);

        this.container.addChild(this._coverContainer);

        this._cover = cover;

        this._step = {};

        animations.addLabel("beginning");

        if (slide.animateTransition) {
            animations.add(TweenLite.fromTo(this._step, 1.5, {coverTransition: 0}, {coverTransition: 1}));
        }
        else {
            cover.transitionStep = 1;
        }

        this._animations.addLabel("after-transition");

        this._fnReady = this.init();

        this._invalid = (() => {
            if (this._fnReady) {
                this._pReady = this._fnReady();
                return false;
            }
            else {
                return true;
            }
        })();

    }

    /**
     * Override for each theme
     * @returns
     */
    init() {
        return false;
    }

    /**
     * Override for each theme
     */
    animate() {
        if (this.slide.animateTransition) {
            this._cover.transitionStep = this._step.coverTransition;
        }
    }

    isReady() {
        return this._pReady;
    }

    createContainer() {
        return createContainer.apply(this, arguments);
    }

    createGraphics() {
        return createGraphics.apply(this, arguments);
    }

    get invalid() {
        return this._invalid;
    }

    get coverImage() {
        return this._cover.image;
    }
    set coverImage(image) {
        this._cover.image = image;
    }

    get slide() {
        return this._slide;
    }

    get container() {
        return this.slide.container;
    }

    get animations() {
        return this._animations;
    }

    get cover() {
        return this._cover;
    }

    setCoverAnimation(type) {

        switch(type) {

            case 'slide':

                const SLIDE_MOVE_OFFSET = 30;

                this._cover.width += SLIDE_MOVE_OFFSET;
                this._cover.height += SLIDE_MOVE_OFFSET;

                let coverDirection = {
                    x: Math.round(Math.random()*10)%2===0 ? 1 : -2,
                    y: Math.round(Math.random()*10)%2===0 ? 1 : -2
                };

                let start = {
                    x: coverDirection.x > 0 ? -SLIDE_MOVE_OFFSET : 0,
                    y: coverDirection.y > 0 ? -SLIDE_MOVE_OFFSET : 0
                };

                this._animations.add([
                    TweenLite.fromTo(this._coverContainer.scale, 5, {x:1,y:1}, {x:"+=0.1",y:"+=0.1"}),
                    TweenLite.fromTo(this._coverContainer, 5, {x:start.x}, {x:`+=${SLIDE_MOVE_OFFSET*coverDirection.x}`}),
                    TweenLite.fromTo(this._coverContainer, 5, {y:start.y}, {y:`+=${SLIDE_MOVE_OFFSET/2*coverDirection.y}`})
                ], 'beginning');

                break;

            default:
                throw new Error(`unknown animation "${type}`);
        }

    }

    pause() {
        this._animations.pause();
    }

    play() {
        this._animations.play();
    }

    stop() {
        this._animations.pause();
        this._animations.progress(0);
    }

    progress(value) {
        this._animations.progress(value);
    }

    restart() {
        this._animations.restart();
    }
}

class SliderContainer extends Container {

    addGraphics(options) {

        let graphics = createGraphics({ ...options });

        this.addChild(graphics);

        return graphics;
    }
}

function createText({position:[x,y], style, text}) {
    let object = new PIXI.Text(text, style);
    object.x = x;
    object.y = y;
    return object;
}

function drawShadow({graphics, position:[x,y], dimensions:[width,height], skew=0, depth=0}) {

    let step = 1;

    while(depth--) {

        x -= step;
        y -= step;
        width += step * 2;
        height += step * 2;

        graphics.beginFill(0x00, 0.1);
        graphics.moveTo(x, y);
        graphics.lineTo(width + skew, y);
        graphics.lineTo(width, y + height);
        graphics.lineTo(x, y + height);
        graphics.endFill();
    }
}

function createContainer({position,shadow=0}={}) {

    let container = new SliderContainer();

    container.x = position && position[0] || 0;
    container.y = position && position[1] || 0;

    return container;
}

function createGraphics({fill, position, dimensions, skew=0, shadow=0, text}) {

    let x = position && position[0] || 0;
    let y = position && position[1] || 0;
    let width = dimensions && dimensions[0] || 0;
    let height = dimensions && dimensions[1] || 0;

    let container = new Container();
    let graphics = new Graphics();

    container.addChild(graphics);

    if (text) {

        let margin = text.margin || [0,0,0,0];

        let textObject = createText({
            position: [x+margin[3],y+margin[0]],
            text: text.content,
            style: text.style
        });

        container.addChild(textObject);

        let textMetrics = TextMetrics.measureText(text.content, text.style);

        width = textMetrics.width + margin[1]+margin[3];
        height = textMetrics.height + margin[0]+margin[2];
    }

    drawShadow({
        graphics,
        position: [x,y],
        dimensions: [width,height],
        skew,
        depth: shadow
    });

    graphics.beginFill(fill.color, fill.alpha||1);
    graphics.moveTo(x + (skew > width ? skew : 0), y);
    graphics.lineTo(x + width + (skew > width ? 0 : skew), y);
    graphics.lineTo(x + width - (skew > width ? skew : 0), y + height);
    graphics.lineTo(x, y + height);
    graphics.endFill();

    return container;
}