import * as PIXI from 'pixi.js';
import url from 'url';
import xhr from 'xhr';
import htmlparser from 'htmlparser2';
import loadIframe from 'load-iframe';


export function getParameterByName(name, url) {
    if (!url) url = window.location.href;
    name = name.replace(/[\[\]]/g, '\\$&');
    var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)'),
        results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, ' '));
}

export function resolveUrl(uri) {
    return url.resolve(window.location.href, uri);
    // if (/^http/.test(uri)) {
    //     return location.protocol + uri.replace(/^https?:/,'');
    // }
    // else {
    //     return location.protocol + '//' + location.hostname + uri;
    // }
}

export function ensureProtocol(href, protocol) {
    if (href.startsWith('http')) {
        if (!protocol) {
            protocol = window.location.protocol;
        }
        return href.replace(/^https?:/, protocol);
    }
    else {
        return href;
    }
}

export function loadIframeDocument(url) {
    return new Promise((resolve, reject) => {
        let iframe = loadIframe(ensureProtocol(url), (err) => {
            if (err) {
                reject(err);
            }
            else {
                try {
                    resolve(iframe.contentWindow.document.documentElement);
                }
                catch(err) {
                    reject(err);
                }
            }
        });
    });
}

export function updateQueryStringParameter(uri, key, value) {
    var re = new RegExp("([?&])" + key + "=.*?(&|$)", "i");
    var separator = uri.indexOf('?') !== -1 ? "&" : "?";
    if (uri.match(re)) {
        return uri.replace(re, '$1' + key + "=" + value + '$2');
    }
    else {
        return uri + separator + key + "=" + value;
    }
}

export function isCurrentUrl(url) {
    if (url.charAt(0) === '/') {
        return window.location.pathname === url;
    }
    else if (/^http/.test(url)) {
        return window.location.href.replace(/\?.*$/,'').replace(/\/$/, '') === ensureProtocol(url).replace(/\/$/, '');
    }
    else {
        return window.location.pathname.split('/').pop() === url;
    }
}

export function getHTML(url) {

    url = ensureProtocol(url);

    return new Promise((resolve, reject) => {
        xhr({
            method: "get",
            uri: url
        }, function (err, resp, body) {
            if (err) {
                reject(err);
            }
            else {
                resolve(body);
            }
        })
    });
}

export function extractPageMetadata(html, opts) {

    let isHead = false, current, res = {
        title: ''
    };

    let parser = new htmlparser.Parser({
        onopentag: (name, attrs) => {
            current = name;
            if (name === 'head') {
                isHead = true;
            } else if (name === 'meta') {
                const meta = parseMeta(attrs, opts.rx);
                if (meta && !res[meta[0]]) {
                    res[meta[0]] = meta[1];
                }
            } else if (name === 'img') {
                const src = attrs.src;
                if (src && src.substr(0, 4) !== 'data') {
                    if (!res.images) {
                        res.images = new Set();
                    }
                    res.images.add(url.resolve(opts.uri, src));
                }
            }

            // if (isHead && name === 'link') {
            //     const feed = parseFeed(attrs);
            //     if (feed) {
            //         if (!res.feeds) {
            //             res.feeds = [];
            //         }
            //         res.feeds.push(feed);
            //     }
            // }
        },
        ontext: text => {
            if (isHead && current === 'title') {
                res.title += text.trim();
            }
        },
        onclosetag: name => {
            if (name === 'head') {
                isHead = false;
            }
        }
    }, {decodeEntities: true});

    parser.write(html);

    parser.end();

    return res;

}

function fixName(name) {
    return name.replace(/(?::|_)(\w)/g, (matches, letter) => letter.toUpperCase());
}

function parseMeta(attrs, rx) {
    const name = attrs.name || attrs.property || Object.keys(attrs)[0];

    if (rx.test(name)) {
        return [
            fixName(name),
            attrs.content || attrs[name]
        ]
    }
}

export function textTruncate(text, style, maxLines) {
    if (maxLines === Infinity) {
        return text;
    }
    const { wordWrapWidth } = style;
    const pixiStyle = new PIXI.TextStyle(style);
    const { lines } = PIXI.TextMetrics.measureText(text, pixiStyle);
    let newText = text;
    if (lines.length > maxLines) {
        const truncatedLines = lines.slice(0, maxLines);
        const lastLine = truncatedLines[truncatedLines.length - 1];
        const words = lastLine.split(' ');
        const wordMetrics = PIXI.TextMetrics.measureText(`\u00A0\n...\n${words.join('\n')}`, pixiStyle);
        const [spaceLength, dotsLength, ...wordLengths] = wordMetrics.lineWidths;
        const { text: newLastLine } = wordLengths.reduce((data, wordLength, i) => {
            if (data.length + wordLength + spaceLength >= wordWrapWidth) {
                return { ...data, length: wordWrapWidth };
            }
            return {
                text: `${data.text}${i > 0 ? ' ' : ''}${words[i]}`,
                length: data.length + wordLength + spaceLength,
            };
        }, { text: '', length: dotsLength });
        truncatedLines[truncatedLines.length - 1] = `${newLastLine}...`;
        newText = truncatedLines.join('\n');
    }
    return newText;
}

export function once(emitter, eventType) {
    return new Promise((resolve) => {
        return emitter.once(eventType, (data) => {
            resolve(data);
        });
    });
}