import ContentAdapterBase from "./base";

import {
    updateQueryStringParameter,
    isCurrentUrl,
    getHTML,
    extractPageMetadata,
    resolveUrl,
    loadIframeDocument} from "../lib/utils";

export default class DynamicContentAdapter extends ContentAdapterBase {

    constructor(options) {
        super();
        this._options = options;
    }

    async refresh() {
        let document = await this._getMainPageDocument();
        let list = await this.getContentList(document) || [];
        for(let content of list) {
            let details = await this.getContentDetails(content).catch((err) => {
                console.error(err);
                return content;
            });
            this.emit('content', [details]);
        }

    }

    get rxMeta() {
        return /charset|description|keywords|twitter:|og:|vk:|al:|theme-color/im;
    }

    getMainPage() {
        if (this._options.getMainPage) {
            return this._options.getMainPage();
        }
        ContentAdapterBase._throw('getMainPage not implemented');
    }

    getContentList(document) {
        if (document) {
            let fn = (() => {
                switch(typeof this._options.getContentList) {
                    case 'function':
                        return this._options.getContentList;
                    case 'string':
                        return new Function("$", this._options.getContentList);
                }
            })();
            if (fn) {
                return fn(document);
            }
            ContentAdapterBase._throw('getContentList not implemented');
        }
    }

    shouldExtractPageMetadata(content) {
        return !(content.coverSource && content.titleSource);
    }

    async getContentDetails(content) {

        if (content.link && this.shouldExtractPageMetadata(content)) {

            let href = resolveUrl(content.link);

            let html = await getHTML(href).catch((err) => {
                console.error(err);
            });

            if (html) {
                let metadata = extractPageMetadata(html, {
                    rx: this.rxMeta,
                    uri: href
                });
                content = {
                    ...content,
                    title: content.titleSource == 'mainPage' ? content.title : metadata.ogTitle || metadata.title || content.title,
                    cover: content.coverSource == 'mainPage' ? content.cover : metadata.ogImage || content.cover
                };
            }
        }



        return content;
    }

    async _getMainPageDocument() {

        let mainPage = resolveUrl(await this.getMainPage());

        if (isCurrentUrl(mainPage)) {
            return window.document.documentElement;
        }
        else {
            return loadIframeDocument(updateQueryStringParameter(mainPage, 'cg', true));
        }

    }



};