import createPlayer from 'web-audio-player';

export default class Audio {

    constructor(options={}) {

        if (options.src) {

            this._audio = createPlayer(options.src, {
                loop: true,
                crossOrigin: true,
                volume: options.muted ? 0 : 1
            });

            this._ready = new Promise((resolve) => {
                this._audio.on('load', () => {
                    this._audio.node.connect(this._audio.context.destination);
                    resolve();
                });
            });
        }
    }

    ready() {
        return this._ready;
    }

    play(autoplay) {
        if (this._audio) {
            if (autoplay) {
                this._autoplayStartTime = new Date();
            }
            else {
                this._audio.play();
                if (this._autoplayStartTime) {
                    this._audio.element.currentTime = Math.floor((new Date() - this._autoplayStartTime) / 1000)
                }
            }

            this._playing = true;
        }
    }

    pause() {
        if (this._audio) {
            this._audio.pause();
            this._paused = true;
            this._playing = false;
        }
    }

    stop() {
        if (this._audio) {
            this._audio.stop();
            this._paused = false;
            this._playing = false;
        }
    }

    get volume() {
        if (this._audio) {
            return this._audio.volume;
        }
    }
    set volume(vol) {
        if (this._audio) {
            this._audio.volume = vol;
            this._refresh();
        }
    }

    _refresh() {
        if (this._playing && !this._audio.playing) {
            this.play();
        }
    }
}