const cache = new Map();

export default class Loader {

    static loadImage(url) {
        return new Promise((resolve) => {
            if (cache.has(url)) {
                return resolve(cache.get(url));
            }
            let image = new Image();
            image.src = url;
            image.addEventListener("load", () => {
                cache.set(url, image);
                resolve(image);
            });
        });
    }
}